import { CLOUD_URL_PREFIX } from '../utils';
import axios from 'axios';


export function getArticleList() {
  return new Promise((resolve, reject) => {
    try {
      axios.get(`${CLOUD_URL_PREFIX}/listData.js`).then((res) => {
        resolve(res.data.list);
      }, (error) => {
        console.log(error);
        reject();
      })
    } catch (error) {
      console.log(error);
      reject();
    }
  });
}



