import React from 'react';
import styles from './List.module.scss'
import { getArticleList } from '../../api/ArticlesAPI';
import { IArticle, ObjArrMap, ObjStringMap } from '../../types/index';
import ListItem from './ListItem';
import { Divider, Switch, Icon, BackTop } from 'antd';
import { useEventListener, searchMap } from '../../utils';
import deepClone from 'deep-clone';

import AutoResponsive from "autoresponsive-react";

export default function List() {
  const getCurClientWidth = function () {
    return Math.floor(document.body.clientWidth * 0.96)
  }
  const [sourceList, setSourceList] = React.useState<Array<IArticle>>([]);
  const [list, setList] = React.useState<Array<IArticle>>([]);
  const [clientWidth, setClientWidth] = React.useState(getCurClientWidth());
  const autoResponsiveProps = {
    itemMargin: 10,
    itemClassName: 'articleItem',
    transitionDuration: '.5',
    gridWidth: 10,
    transitionTimingFunction: 'easeIn',
    containerWidth: clientWidth,
  };
  const resize = function () {
    setClientWidth(getCurClientWidth);
  }
  useEventListener('resize', resize);
  const newestArr = [{ 'id': 'publish', name: '最新发布' }, { id: '' }, { 'id': 'comment', name: '最新评论' }];
  const [newestType, setNewestType] = React.useState<string>(`publish`);
  function changeNewest(type: string) {
    setNewestType(type);
    if (type == 'publish') {
      sortByPublish();
    } else {
      sortByComment();
    }
  }

  function sortByPublish(effectiveList?: Array<IArticle>) {
    let arr = effectiveList || deepClone(list);
    arr.sort((a, b) => {
      return new Date(b.publishTime).getTime() - new Date(a.publishTime).getTime();
    })
    setList(arr);
  }
  function sortByComment() {
    let arr = deepClone(list);
    arr.sort((a, b) => {
      return new Date(b.lastResponse || "1970-12-12 00:00:00").getTime() - new Date(a.lastResponse).getTime();
    })
    setList(arr);
  }
  function filterEffectiveData(list: Array<IArticle>) {
    let arr = deepClone(list);
    arr = arr.filter((item) => {
      return item.link
    })
    return arr;
  }
  const categorys = [{ 'id': 'all', name: '全部' }, { 'id': 'type', name: '类型' }, { 'id': 'place', name: '位置' }, { 'id': 'hobby', name: '爱好' }, { 'id': 'work', name: '工作' }, { 'id': 'condition', name: '条件' }, { 'id': 'tag', name: '标签' }];
  const typeArr = [{ 'id': 'all', name: '全部' }, { 'id': 'makeFriends', name: '征友' }, { 'id': 'discuss', name: '讨论' }, { 'id': 'help', name: '互助' }, { 'id': 'share', name: '分享' }, { 'id': 'diary', name: '日记' },];
  const placeArr = [{ 'id': 'all', name: '全部' }, { 'id': 'shenzhen', name: '深圳' }, { 'id': 'guangzhou', name: '广州' }, { 'id': 'beijing', name: '北京' }, { 'id': 'shanghai', name: '上海' }, { 'id': 'chengdu', name: '成都' }, { 'id': 'hangzhou', name: '杭州' }, { 'id': 'otherPlace', name: '其他' },];
  const hobbyArr = [{ 'id': 'all', name: '全部' }, { 'id': 'sports', name: '运动' }, { 'id': 'game', name: '游戏' }, { 'id': 'music', name: '音乐' }, { 'id': 'travel', name: '旅行' }, { 'id': 'food', name: '美食' }, { 'id': 'takePicture', name: '摄影' }, { 'id': 'pet', name: '宠物' }, { 'id': 'otherHobby', name: '其他' },];
  const workArr = [{ 'id': 'all', name: '全部' }, { 'id': 'internet', name: '互联网' }, { 'id': 'private', name: '个人' }, { 'id': 'financial', name: '金融' }, { 'id': 'education', name: '教育' }, { 'id': 'student', name: '学生' }, { 'id': 'otherWork', name: '其他' },];
  const conditionArr = [{ 'id': 'all', name: '全部' }, { 'id': '1', name: '1' }, { 'id': '0', name: '0' }, { 'id': '0.5', name: '0.5' }, { 'id': 'out', name: '出柜' }, { 'id': 'formMarriage', name: '形婚' }, { 'id': 'neverMarriage', name: '不婚' }, { 'id': 'surrogacy', name: '代孕' }, { 'id': 'stable', name: '稳定' }, { 'id': 'otherCondition', name: '其他' },];
  const tagArr = [{ 'id': 'all', name: '全部' }, { 'id': 'house', name: '租房' }, { 'id': 'highEducation', name: '高学历' }, { 'id': 'aids', name: '艾滋' }, { 'id': 'depression', name: '抑郁' }, { 'id': 'spring', name: '春节' }, { 'id': 'open', name: '开放式' }, { 'id': 'otherTag', name: '其他' },];
  const categoryArrMap: ObjArrMap = {
    type: typeArr,
    place: placeArr,
    hobby: hobbyArr,
    work: workArr,
    condition: conditionArr,
    tag: tagArr,
  };
  const [category, setCategory] = React.useState<string>(`all`);
  const [conditions, setConditions] = React.useState<ObjStringMap>({
    type: 'all',
    place: 'all',
    hobby: 'all',
    work: 'all',
    condition: 'all',
    tag: 'all',
  });
  const [secondSearch, setSecondSearch] = React.useState<string>(`all`);
  const [secondFiltered, setSecondFiltered] = React.useState<boolean>(true);
  const [switchCheck, setSwitchCheck] = React.useState<boolean>(false);
  function setCurSecondSearch(type: string) {
    let conditions_bak = deepClone(conditions);
    conditions_bak[category] = type;
    setConditions(conditions_bak);
    setSecondSearch(type);
    setSecondFiltered(false);
  }
  function changeCategory(type: string) {
    setCategory(type)
  }
  function onSwitchChange(checked: boolean) {
    setSwitchCheck(checked);
    setSecondFiltered(false);
  }
  function doSecondSearch() {
    if (sourceList.length && !secondFiltered) {
      let list_bak = deepClone(sourceList);
      let keysStr = '';
      for (let k in conditions) {
        let curCondition = conditions[k];
        if (curCondition !== 'all') {
          keysStr += searchMap[curCondition].join(',') + ',';
        }
      }
      if (keysStr) {
        list_bak = list_bak.filter((article) => {
          let matchedArr = [];
          for (let word of article.keyWords) {
            if (keysStr.indexOf(word) > -1) {
              matchedArr.push(word);
              break;
            }
          }
          return matchedArr.length;
        });
      }
      list_bak = list_bak.filter((article) => {
        if (switchCheck) {
          return article.imgLinks && article.imgLinks.length && article.frontContent.length > 50;
        }
        return true;
      });
      setList(list_bak);
      setSecondFiltered(true);
    }
  }
  React.useEffect(() => {
    getArticleList().then((data: any) => {
      let effectiveData = filterEffectiveData(data)
      setSourceList(deepClone(effectiveData));
      sortByPublish(effectiveData);
    });
  }, []);
  doSecondSearch();
  return (
    <div id="home-list" className={styles.homeList}>
      <BackTop />
      <div className={styles.htmlContainer}>
        {/* <Select showSearch onChange={onPlaceChange} style={{ width: 200 }} placeholder="选择地点关键词">
          {
            placeArr.map((val, index) => {
              return <Option key={index} value={val}>{val}</Option>
            })
          }
        </Select> */}
        <div className={styles.searchContainer}>
          <div className={styles.searchTop}>
            <div className={styles.left}>
              {
                newestArr.map((newestObj, index) => {
                  if (!newestObj.id) {
                    return <Divider key={20} type="vertical" />
                  } else {
                    return <span key={index} className={newestType === newestObj.id ? styles.cur : ''} onClick={() => {
                      changeNewest(newestObj.id);
                    }}>{newestObj.name}</span>
                  }
                })
              }
            </div>
            <div className={styles.right}>
              <Switch
                onChange={onSwitchChange}
                defaultChecked={switchCheck}
                checkedChildren={<span className={styles.switchSpan}><Icon type="check" />诚意贴</span>}
                unCheckedChildren={<span className={styles.switchSpan}><Icon type="close" />诚意贴</span>}
              />
            </div>
          </div>
          <div className={styles.searchCategory}>
            <ul>
              {
                categorys.map((categoryObj, index) => {
                  return <li onClick={() => {
                    changeCategory(categoryObj.id);
                  }} key={index} id={categoryObj.id} className={category === categoryObj.id ? styles.cur : ''} >{categoryObj.name}</li>
                })
              }
            </ul>
          </div>
          <div className={styles.secondSearch}>
            <ul>
              {
                category && category != 'all' && categoryArrMap[category].map((item, index) => {
                  return <li onClick={() => {
                    setCurSecondSearch(item.id);
                  }} key={item.id + index} id={item.id} className={secondSearch === item.id ? styles.cur : ''} >{item.name}</li>
                })
              }
            </ul>
          </div>
        </div>
        <div className={styles.listContainer}>
          {<AutoResponsive   {...autoResponsiveProps} >
            {
              list.map((item, index) => {
                if (!item.keyWords) item.keyWords = [];
                const itemWidth = Math.floor((clientWidth / 2 - 16));
                const titleHeight = 48, contentHeight = item.frontContent ? 54 : 0, authorHeight = 24, dataHeight = 54, tagHeight = (Math.ceil(item.keyWords.length / 3)) * 22;
                const bottomHeight = titleHeight + contentHeight + authorHeight + dataHeight + tagHeight + 92;//50 margin
                const itemHeight = item.imgRate ? (itemWidth * item.imgRate + bottomHeight) : bottomHeight;
                return <ListItem key={item.link + index} newestType={newestType} article={item} style={{ width: itemWidth, height: itemHeight }} />
              })
            }
          </AutoResponsive>
          }
        </div>
      </div >
    </div>
  );
}
