import React from 'react';
import styles from './ListItem.module.scss';
import { IArticle } from '../../types';
import { ALT_IMAGE_URL } from '../../utils';
import { Tag } from 'antd';
type ListItemProps = {
  article: IArticle,
  newestType: string,
  style: any
};

export default function ListItem({
  article,
  style,
  newestType
}: ListItemProps) {

  React.useEffect(() => {
  }, []);
  const hasImg = article.imgLinks && article.imgLinks.length;
  const colors = ['volcano', 'blue', 'cyan', 'gold', 'geekblue', 'magenta', 'lime', 'red'];
  const noImgStyle = hasImg ? '' : styles.noImg;
  const showPublishTime = (newestType === 'publish');

  return (
    <div className={`${styles.listItem} ${noImgStyle}`} style={style}>
      <a href={article.link} className={styles.card}>
        {
          hasImg ? <div className={styles.imgContainer}>
            <img src={`${article.imgLinks[0]}`} width={style.width} height={style.width * article.imgRate} />
          </div> : ''
        }
        <div className={styles.textContainer}>
          <div className={styles.dataContainer}>
            <span title={article.isEliteArticle ? `精华帖 - ${article.title}` : article.title}>
              <span className={styles.title}>{article.isEliteArticle ? <i className='icon-heart'>精</i> : ''}{`${article.title}`}</span>
            </span>
          </div>
          <div className={styles.dataContainer}>
            <span title={`内容：${article.frontContent}`}>
              <span className={styles.frontContent}>{article.frontContent}</span>
            </span>
          </div>
          <div className={styles.dataContainer}>
            <span title={`作者：${article.author}`}>
              <img className={styles.authorPic} src={article.authorPic} />
              <span className={styles.author}>{article.author}</span>
            </span>
          </div>
          <div className={styles.dataContainer}>
            <span className={styles.dataItem} title={`文章内容长度：${article.contentLength}`}>
              <i className={`icon-contentLength`}></i>
              <span className={styles.text}>文长{article.contentLength}</span>
            </span>
            <span className={styles.dataItem} title={`照片数量：${hasImg ? article.imgLinks.length : 0}`}>
              <i className={`icon-photo`}></i>
              <span className={styles.text}>照片{hasImg ? article.imgLinks.length : 0}</span>
            </span>
          </div>
          <div className={styles.dataContainer}>
            <span className={styles.dataItem} title={`评论数：${article.responseCount}`}>
              <i className={`icon-response2`}></i>
              <span className={styles.text}>评论{article.responseCount}</span>
            </span>
            <span className={styles.dataItem} title={`点赞数：${article.likeCount}`}>
              <i className={`icon-like`}></i>
              <span className={styles.text}>点赞{article.likeCount}</span>
            </span>
          </div>
          <div className={styles.dataContainer}>
            <span className={styles.keyWordContainer} title={`关键词：${article.keyWords.join('，')}`}>
              {
                article.keyWords.map((keyWord, keyIndex) => {
                  return <Tag key={keyIndex} className={styles.tag} color={colors[keyIndex % 8]}>{keyWord}</Tag>
                })
              }
            </span>
          </div>
          <div className={`${styles.dataContainer} ${styles.publishTimeContainer}`}>
            <span >
              <i className={`icon-time`}></i>
              <span className={styles.publishTime}>{showPublishTime ? article.publishTime : article.lastResponse}</span>
            </span>

          </div>
        </div>
      </a >
    </div >
  );
}

